#create-tool {
  background-color: rgb(207, 206, 206);
  width: 400px;
  margin: auto;
  border-radius: 4px;
  align-content: center;
}

#create-listing {
  background-color: rgb(207, 206, 206);
  width: 450px;
  margin: auto;
  border-radius: 4px;
}

.label-input {
  height: 35px;
}

label {
  font-weight: bold;
  display: inline-block;
  width: 150px;
  margin-right: 10px;
  text-align: right;
}

.cards {
  /* background-color: rgb(207, 206, 206); */
  display: grid;
  margin: auto;
  justify-self: center;
  grid-template-columns: repeat(3, 200px);
  grid-template-rows: repeat(auto-fill, 250px);
  /* grid-gap: 20px; */
  /* float: left; */
  width: 630px;
}

.listing-card {
  background-color: rgb(207, 206, 206);
  display: grid;
  margin: auto;
  justify-self: center;
  width: 200px;
  height: 250px;
  border-radius: 5px;
}

.listing-card:hover {
  transform: scale(1.03);
}

.image-card {
  object-fit: cover;
  width: 190px;
  height: auto;
  margin-left: 5px;
}